<template>
	<div class="my-meeting-container" id="my-meeting-container">
        <user-management-nav class="user-management-nav" :list='navList'></user-management-nav>
        <div class="user-query pull-right">
            <form action="" class="form-inline">
                <div class="form-group">
                    <label for=""></label>
                    <input type="text" class="form-control" value="">
                </div>
                <div class="form-group">
                    <label for=""></label>
                    <select name="" id="" class="form-control">
                        <option v-for="department in basicInfo.departments"  value="" >{{department}}</option>
                    </select>
                </div>
                <div class="btn btn-default" > <span class="glyphicon  glyphicon-query"></span></div>
            </form>
        </div>
	    <table class="user-table table table-striped table-hover table-bordered">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Young Jack</td>
                    <td class="info" ></td>
                    <td></td>
                    <td>
                        <button class="btn btn-warning" @click="deleteUser"></button>
                    </td>
                </tr>
                <tr>
                    <td>Young Jack</td>
                    <td class="info" ></td>
                    <td></td>
                    <td>
                        <button class="btn btn-warning" @click="deleteUser"></button>
                    </td>
                </tr>
                <tr>
                    <td>Young Jack</td>
                    <td class="info" ></td>
                    <td></td>
                    <td>
                        <button class="btn btn-warning" @click="deleteUser"></button>
                    </td>
                </tr>
                <tr>
                    <td>Young Lo</td>
                    <td class="success"></td>
                    <td></td>
                    <td>
                        <button disabled="" class="btn btn-warning" @click="deleteUser"></button>
                    </td>
                </tr>
                <tr>
                    <td>Young Lo</td>
                    <td class="success"></td>
                    <td></td>
                    <td>
                        <button disabled="" class="btn btn-warning" @click="deleteUser"></button>
                    </td>
                </tr>
            </tbody>

        </table>
        <button @click.prevent="addUser" class="btn btn-info pull-right"></button>
        <edit-panel edit-mode="user" v-if="isEditing" :info="basicInfo" @quit-edit="isEditing=false" ></edit-panel>
    </div>
            
</template>

<script>
	
    import UserManagementNav from './ContentNav.vue'
	import EditPanel from './EditPanel.vue'
	
	export default {
		name: 'UserManagement',
		data () {
			return {
				navList: [
							{
								text: '',
								isActive: true
							},
							{
								text: '',
								isActive: false
							},
							{
								text: '',
								isActive: false
							},
						],
                basicInfo: {
                    title: '',
                    systemRoles: ['',''],
                    departments: ['','','']
                },
						
				isEditing: false		
			}
		},
		methods: {
			addUser () {
                this.isEditing = true
            },
            deleteUser () {
                swal({
                    title: '?',
                    text: 'Young Jack',
                    type: 'warning',
                    showCancelButton: true
                })
            }
		},
		components:{ EditPanel,UserManagementNav}
	}
</script>

<style>
    .user-query{
        margin-top: -20px;
        margin-bottom: 20px;
    }
</style>





