                    <td>
                        <button @click="edit" class="btn btn-info"></button>
                        <button class="btn btn-warning" @click.prevent="cancel" ></button>
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>2017-08-14 11:00</td>
                    <td></td>
                    <td ></td>
                    <td>
                        <button @click="edit" class="btn btn-info"></button>
                        <button class="btn btn-warning" @click.prevent="cancel" ></button>
