</template>

<script>
	import MyMeetingNav from './ContentNav.vue'
	import EditPanel from './EditPanel.vue'
	
	export default {
		name: 'myMeeting',
		data () {
			return {
				navList: [
							{
								text: '',
								isActive: true
							},
							{
								text: '',
								isActive: false
							},
							{
