				isEditing: false		
			}
		},
		methods: {
			cancel () {
				swal({
					title: '',
					type: 'warning',
					text:'',
					showCancelButton: true
				})
			},
			edit () {
				this.isEditing = true
				this.$emit('edit-meeting')
			}
		},
		components:{MyMeetingNav, EditPanel}
	}
</script>




