<template>
	<div class="my-meeting-container" id="my-meeting-container">
		<device-management-nav class="device-management-nav" :list='navList'></device-management-nav>                
        <table class="my-meeting-table table table-striped table-hover table-bordered">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="info"></td>
                    <td>
                        <button class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="info"></td>
                    <td>
                        <button class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="info"></td>
                    <td>
                        <button class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="info"></td>
                    <td>
                        <button class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="info"></td>
                    <td>
                        <button class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
                    <td>001</td>
                    <td></td>
                    <td></td>
                    <td class="warning"></td>
                    <td>
                        <button disabled="" class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button disabled="" class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
                <tr>
                    <td>-1</td>
