                    <td>
                        <button disabled="" class="btn btn-info" @click.prevent="setDevice" ></button>
                        <button disabled="" class="btn btn-warning" @click.prevent="deleteDevice" ></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <button class="btn btn-info pull-right" @click="addDevice" ></button>
        <edit-panel edit-mode="device" v-if="isEditing" @quit-edit="isEditing=false" :info="basicInfo" ></edit-panel>
    </div>
            
</template>

<script>
	import DeviceManagementNav from './ContentNav.vue'
	import EditPanel from './EditPanel.vue'
	
	export default {
		name: 'deviceManagement',
		data () {
			return {
				navList: [
							{
								text: '',
								isActive: true
							},
                            {
                                text: '',
                                isActive: false
                            },
							{
								text: '',
								isActive: false
							},
						],
                deviceInfo: {
                    meetRoom: '',

                },
                basicInfo: {
                    title: '',
                    deviceTypes: ['',''],
                    meetingRooms: ['','',''] 
                },	
				isEditing: false		
			}
		},
		methods: {
			deleteDevice () {
                swal({
                    title: ' ',
                    text: ' -1',
                    type: 'warning',
                    showCancelButton: true
                })
            },
            addDevice () {
                this.isEditing = true
                this.basicInfo.title = ""
            },
            setDevice () {
                this.isEditing = true
            }
		},
		components:{DeviceManagementNav, EditPanel}
	}
</script>




